/* ShoppingCartIcon.css */
.shopping-cart-icon {
  position: relative;
  font-size: 1.5rem;
  color: #6c757d;
}

.cart-count {
  position: absolute;
  top: -10px;
  right: -10px;
  background-color: #dc3545;
  color: white;
  border-radius: 50%;
  padding: 2px 6px;
  font-size: 0.8rem;
}
